import { User, UserAction } from '../../actions/user-actions';
import { UserActions } from '../../constants/action-constants';

const initialState: User = {
  login: null,
  password: null,
  isLogined: false,
};

const userSelected = (state = initialState, action: UserAction): User => {
  switch (action.type) {
    case UserActions.SELECT_USER: {
      if (!Array.isArray(action.payload)) {
        return {
          ...action.payload,
        };
      }
      return state;
    }
    default:
      return state;
  }
};

export { userSelected };
