import React, { Fragment, useEffect, useState } from 'react';
import { Waiter } from '../shared/waiter/waiter';
import { useSelector } from 'react-redux';
import { RootState } from '../../store/store';
import {
  AuthCallback,
  useAuthorization,
} from '../custom-hook/use-authorization';
import { UserActions } from '../../store/constants/action-constants';
import { ModalInfo } from '../shared/modal-info/modal-info';
import { useNavigate } from 'react-router-dom';

const ConnectionWaiter: React.FC = () => {
  const connectionText = 'Подключение к серверу';

  const user = useSelector((store: RootState) => store.users.userCurrent);

  const isConnected = useSelector((store: RootState) => store.socket.isOpen);
  const [isShowWaiter, setShowWaiter] = useState(!isConnected);

  const [isShowMessage, setShowMessage] = useState(false);

  const [isReLogin, setReLogin] = useState(false);

  const authCallback: AuthCallback = {
    pending: setReLogin.bind(this, false),
    fullfilled: setShowWaiter.bind(this, false),
    rejected: setShowMessage.bind(this, true),
  };

  const [messageResult, authSendHandler, reset] = useAuthorization(
    user,
    UserActions.LOGIN,
    authCallback
  );

  const navigate = useNavigate();
  const redirectToLogin = () => {
    setShowMessage(false);
    reset();
    navigate('/login');
  };

  useEffect(() => {
    if (!isConnected && user.login !== null && user.password !== null) {
      setReLogin(true);
    }
    if (isConnected && isReLogin) {
      authSendHandler();
    }
    setShowWaiter(!isConnected);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isConnected, isReLogin, user.login, user.password]);

  return (
    <Fragment>
      {isShowWaiter && <Waiter text={connectionText} />}
      {isShowMessage && (
        <ModalInfo text={messageResult} callback={redirectToLogin} />
      )}
    </Fragment>
  );
};

export default ConnectionWaiter;
