import { ValidationResult } from '../../../custom-hook/use-validation';

const errorText = 'Используйте прописные и заглавные буквы';

const caseValidator = (value: string): ValidationResult => {
  return {
    result: value.toLowerCase() !== value && value.toUpperCase() !== value,
    message:
      value.toLowerCase() === value || value.toUpperCase() === value
        ? errorText
        : '',
  };
};

export { caseValidator };
