import './App.css';
import React, { Fragment, Suspense, lazy } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { RootState } from './store/store';
import ConnectionWaiter from './view/connection-waiter/connection-waiter';
import Login from './view/auth/Login';
import About from './view/about/About';

const Main = lazy(() => import('./view/main/Main'));
const NotFound = lazy(() => import('./view/not-found/NotFound'));

const App: React.FC = () => {
  const basePath = process.env.REACT_APP_REPOSITORY;
  const isLogined = useSelector(
    (store: RootState) => store.users.userCurrent.login !== null
  );

  return (
    <Fragment>
      <BrowserRouter basename={basePath}>
        <Suspense>
          <Routes>
            <Route
              path="/"
              element={isLogined ? <Main /> : <Navigate to="/login" />}
            />
            <Route
              path="/main"
              element={isLogined ? <Main /> : <Navigate to="/login" />}
            />
            <Route
              path="/login"
              element={!isLogined ? <Login /> : <Navigate to="/main" />} />
            <Route path="/about" element={<About />} />
            <Route path="*" element={<NotFound />} />
          </Routes>
        </Suspense>
        <ConnectionWaiter />
      </BrowserRouter>
    </Fragment>
  );
};

export default App;
