import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions, ServerActions } from '../constants/action-constants';
import { addMessageToHistory, editedMessage } from '../actions/dialog-actions';

export const receiveMessageMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      (action.payload.type === DialogActions.MSG_SEND ||
        DialogActions.MSG_EDIT) &&
      action.payload.payload &&
      'message' in action.payload.payload
    ) {
      const state = getState();
      const currentUser = state.users.userCurrent.login;
      const login =
        currentUser === action.payload.payload.message.from
          ? action.payload.payload.message.to
          : action.payload.payload.message.from;
      if (action.payload.type === DialogActions.MSG_EDIT) {
        dispatch(editedMessage(action.payload.payload.message));
      } else {
        dispatch(addMessageToHistory(login, action.payload.payload.message));
      }
    }

    return next(action);
  };
