import { combineReducers } from 'redux';
import { userCurrent } from './user/user-current-reduser';
import { usersAnother } from './user/user-another-reducer';
import { userSelected } from './user/user-selected-reducer';

const users = combineReducers({
  userCurrent,
  usersAnother,
  userSelected,
});

export default users;
