import { useNavigate } from 'react-router-dom';
import Button from '../shared/button/Button';
import style from './style.module.css';
import React, { useEffect, useState } from 'react';
import { FieldGroup } from '../shared/field-group/Field-group';
import {
  FieldNamed,
  FieldNamedProps,
} from '../shared/field-named/Field-named';
import { lengthValidator } from '../shared/field-named/validator/length-validator';
import { caseValidator } from '../shared/field-named/validator/case-validator';
import { ModalInfo } from '../shared/modal-info/modal-info';
import { Waiter } from '../shared/waiter/waiter';
import { store } from '../../store/store';
import { User, login } from '../../store/actions/user-actions';
import { useInputChange } from '../custom-hook/use-input-change';
import {
  AuthCallback,
  useAuthorization,
} from '../custom-hook/use-authorization';
import { UserActions } from '../../store/constants/action-constants';

const fieldList: FieldNamedProps[] = [
  {
    text: 'Имя',
    placeholder: 'Введите имя',
    callback: () => { },
    type: 'text',
    validators: [lengthValidator],
    name: 'login',
  },
  {
    text: 'Пароль',
    placeholder: 'Введите пароль',
    callback: () => { },
    type: 'password',
    validators: [lengthValidator, caseValidator],
    name: 'password',
  },
];

const Login: React.FC = () => {
  const waitText = 'Авторизация пользователя...';
  const headerText = 'Авторизация';
  const authText = 'Войти в приложение';
  const infoText = 'Инфо';

  const [isShowMessage, setShowMessage] = useState(false);
  const [isWait, setWait] = useState(false);
  const [isDisabledButton, setDisableButton] = useState(true);

  const [userValue, isValid, changeHandler] = useInputChange();

  const authCallback: AuthCallback = {
    pending: () => {
      setWait(true);
    },
    fullfilled: () => {
      setWait(false);
      const user: User = {
        login: userValue.login,
        password: userValue.password,
        isLogined: true,
      };
      store.dispatch(login(user));
      navigate('/main');
    },
    rejected: () => {
      setWait(false);
      setShowMessage(true);
    },
  };
  const [messageResult, authSendHandler, reset] = useAuthorization(
    userValue,
    UserActions.LOGIN,
    authCallback
  );

  const navigate = useNavigate();
  const disableModal = () => {
    setShowMessage(false);
    reset();
  };

  useEffect(() => {
    const isEnable = isValid.login && isValid.password;
    setDisableButton(!isEnable);
  }, [isValid.login, isValid.password]);

  const submitHandler = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    authSendHandler();
  }

  return (
    <form className={style.login} onSubmit={submitHandler}>
      <FieldGroup header={headerText}>
        {fieldList.map((field, index) => (
          <FieldNamed
            text={field.text}
            placeholder={field.placeholder}
            type={field.type}
            callback={changeHandler}
            validators={field.validators}
            name={field.name}
            key={index}
          />
        ))}
      </FieldGroup>
      <Button
        text={authText}
        callback={() => { }}
        disabled={isDisabledButton}
        submit={true}
        fixed={true}
      />
      <Button
        text={infoText}
        callback={() => navigate('/about')}
        fixed={true}
      />
      {isWait && <Waiter text={waitText} />}
      {isShowMessage && (
        <ModalInfo text={messageResult} callback={disableModal} />
      )}
    </form>
  );
};

export default Login;
