import { ServerAction, ServerMessage } from '../actions/server-actions';
import { ServerActions } from '../constants/action-constants';

const initialState = {
  incoming: new Array<ServerMessage>(),
  outcoming: new Array<ServerMessage>(),
};

export const server = (state = initialState, action: ServerAction) => {
  switch (action.type) {
    case ServerActions.INCOMING: {
      return {
        incoming: [...state.incoming, action.payload],
        outcoming: [...state.outcoming],
      };
    }
    case ServerActions.OUTCOMING: {
      return {
        incoming: [...state.incoming],
        outcoming: [...state.outcoming, action.payload],
      };
    }
    default: {
      return state;
    }
  }
};
