import { ThunkMiddleware } from '@reduxjs/toolkit';
import {
  ServerActions,
  UserActions,
} from '../constants/action-constants';
import { getContacts } from '../../core/services/get-contacts';
import { clearMessagesHistory } from '../actions/dialog-actions';

export const authCurrentUserMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      (action.payload.type === UserActions.LOGIN || action.payload.type === UserActions.REGISTRATION)
    ) {
      dispatch(clearMessagesHistory());
      getContacts();
    }

    return next(action);
  };
