import { ThunkMiddleware, configureStore } from '@reduxjs/toolkit';
import rootReducer from './reducers/redusers';
import { getMessagesMiddleware } from './middleware/get-messages-middleware';
import { deliveredMessageMiddleware } from './middleware/delivered-message-middleware';
import { readedMessageMiddleware } from './middleware/readed-message-middleware';
import { receiveMessageMiddleware } from './middleware/receive-message-middleware';
import { setUserMessagesMiddleware } from './middleware/set-user-messages-middleware';
import { setAllInactiveUsersMiddleware } from './middleware/set-all-inactive-users-middleware';
import { setAllActiveUsersMiddleware } from './middleware/set-all-active-users-middleware';
import { authCurrentUserMiddleware } from './middleware/auth-current-user-middleware';
import { authAnotherUserMiddleware } from './middleware/auth-another-user-middleware';
import { deletedMessageMiddleware } from './middleware/deleted-message-middleware';
import { getAuthUser } from '../storage/storage';

const middleware: ThunkMiddleware[] = [
  authCurrentUserMiddleware,
  authAnotherUserMiddleware,
  getMessagesMiddleware,
  deliveredMessageMiddleware,
  readedMessageMiddleware,
  deletedMessageMiddleware,
  receiveMessageMiddleware,
  setUserMessagesMiddleware,
  setAllInactiveUsersMiddleware,
  setAllActiveUsersMiddleware,
];

const preloadedUserState = getAuthUser();

export const store = configureStore({
  reducer: rootReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat(...middleware),
  preloadedState: { users: { userCurrent: preloadedUserState } },
  devTools: process.env.NODE_ENV !== 'production',
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
