import { SocketActions } from '../constants/action-constants';

export type Socket = {
  isOpen: boolean;
};

export type SocketAction = {
  type: SocketActions;
  payload: Socket;
};

export const connectionOpen = (): SocketAction => ({
  type: SocketActions.OPEN,
  payload: {
    isOpen: true,
  },
});

export const connectionClose = (): SocketAction => ({
  type: SocketActions.CLOSE,
  payload: {
    isOpen: false,
  },
});

export const connectionError = (): SocketAction => ({
  type: SocketActions.ERROR,
  payload: {
    isOpen: false,
  },
});
