import { Fragment } from 'react';
import style from './style.module.css';

type WaiterProps = {
  text: string;
};

const Waiter: React.FC<WaiterProps> = ({ text }) => {
  return (
    <Fragment>
      <div className={style.darken}></div>
      <div className={style.loader}>
        <div className={style.waiter}></div>
        <label className={style.info}>{text}</label>
      </div>
    </Fragment>
  );
};

export { Waiter };
export type { WaiterProps };
