import { UserActions } from '../constants/action-constants';

export type User = {
  login: string | null;
  password?: string | null;
  isLogined: boolean;
};

export type AnotherUsers = {
  active: User[];
  inactive: User[];
};

export type UserAction = {
  type: UserActions;
  payload: User | User[];
};

export type AnotherUserAction = {
  type: UserActions;
  payload: AnotherUsers;
};

export const login = (user: User): UserAction => ({
  type: UserActions.LOGIN,
  payload: {
    login: user.login,
    password: user.password,
    isLogined: true,
  },
});

export const logout = (user: User): UserAction => ({
  type: UserActions.LOGOUT,
  payload: {
    login: user.login,
    password: user.password,
    isLogined: false,
  },
});

export const registration = (user: User): UserAction => ({
  type: UserActions.REGISTRATION,
  payload: {
    login: user.login,
    password: user.password,
    isLogined: true,
  },
});

export const loginAnother = (user: User): UserAction => ({
  type: UserActions.LOGIN_ANOTHER,
  payload: {
    login: user.login,
    isLogined: true,
  },
});

export const logoutAnother = (user: User): UserAction => ({
  type: UserActions.LOGOUT_ANOTHER,
  payload: {
    login: user.login,
    isLogined: false,
  },
});

export const allActiveUsers = (usersActive: User[]): UserAction => ({
  type: UserActions.ALL_ACTIVE,
  payload: usersActive,
});

export const allInActiveUsers = (usersInActive: User[]): UserAction => ({
  type: UserActions.ALL_INACTIVE,
  payload: usersInActive,
});

export const selectUser = (user: User): UserAction => ({
  type: UserActions.SELECT_USER,
  payload: user,
});
