import { Socket, SocketAction } from '../actions/socket-actions';
import { SocketActions } from '../constants/action-constants';

const initialState: Socket = {
  isOpen: false,
};

const socket = (state = initialState, action: SocketAction): Socket => {
  switch (action.type) {
    case SocketActions.OPEN:
    case SocketActions.CLOSE:
    case SocketActions.ERROR: {
      return {
        ...state,
        isOpen: action.payload.isOpen,
      };
    }
    default:
      return state;
  }
};

export { socket };
