import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions, ServerActions } from '../constants/action-constants';
import { deletedMessage } from '../actions/dialog-actions';

export const deletedMessageMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === DialogActions.MSG_DELETE
    ) {
      dispatch(deletedMessage(action.payload.payload.message.id));
    }

    return next(action);
  };
