import { ValidationResult } from '../../../custom-hook/use-validation';

const minLength = 4;
const errorText = 'Длина должна более 4 символов';

const lengthValidator = (value: string): ValidationResult => {
  return {
    result: value.length > minLength,
    message: value.length <= minLength ? errorText : '',
  };
};

export { lengthValidator };
