import style from './style.module.css';
import React, { useEffect, useState } from 'react';
import {
  ValidationResult,
  useValidation,
} from '../../custom-hook/use-validation';

type FieldNamedProps = {
  text: string;
  placeholder?: string;
  type?: 'text' | 'password';
  callback: (changedValue: FieldNamedResult) => void;
  validators?: Array<(value: string, valueEqual: string) => ValidationResult>;
  name?: string;
  valueEqual?: string;
};
type FieldNamedResult = {
  name: string;
  value: string;
  isValid: boolean;
};

const FieldNamed: React.FC<FieldNamedProps> = ({
  text,
  placeholder,
  type = 'text',
  callback,
  validators = [],
  name,
  valueEqual = '',
}) => {
  const [value, setValue] = useState('');
  const [isChanged, setChange] = useState(false);

  const [errors, isValid] = useValidation(value, valueEqual, validators);
  useEffect(() => {
    callback({
      name: name || '',
      value: value,
      isValid: isValid,
    });
  }, [name, value, isValid, callback]);

  const keyUpHandler = (event: React.KeyboardEvent<HTMLInputElement>) => {
    setValue(event.currentTarget.value);
    setChange(true);
  };

  return (
    <div className={style['field-container']}>
      <label>{text}</label>
      <div className={style['input-container']}>
        <input
          placeholder={placeholder}
          onKeyUp={keyUpHandler}
          type={type}
          className={
            !isValid && isChanged
              ? [style.error, style['field-input']].join(' ')
              : style['field-input']
          }
        />
        {errors.length > 0 &&
          isChanged &&
          errors.map((error, index) => (
            <label key={index} className={style['error-tooltip']}>
              {error}
            </label>
          ))}
      </div>
    </div>
  );
};

export { FieldNamed };
export type { FieldNamedProps, FieldNamedResult };
