import {
  AnotherUserAction,
  AnotherUsers,
  User,
  UserAction,
} from '../../actions/user-actions';
import { UserActions } from '../../constants/action-constants';

const initialState: AnotherUsers = {
  active: new Array<User>(),
  inactive: new Array<User>(),
};

const usersAnother = (
  state = initialState,
  action: AnotherUserAction | UserAction
): AnotherUsers => {
  switch (action.type) {
    case UserActions.ALL_ACTIVE: {
      if (Array.isArray(action.payload)) {
        return {
          active: new Array(...action.payload),
          inactive: state.inactive,
        };
      }
      return state;
    }
    case UserActions.ALL_INACTIVE: {
      if (Array.isArray(action.payload)) {
        return {
          active: state.active,
          inactive: new Array(...action.payload),
        };
      }
      return state;
    }
    case UserActions.LOGIN_ANOTHER: {
      if ('login' in action.payload) {
        const loginedUser: User = action.payload;
        const inActiveUsers: User[] = state.inactive?.filter(
          (user) => user.login !== loginedUser.login
        );
        return {
          active: [...state.active, loginedUser],
          inactive: [...inActiveUsers],
        };
      }
      return state;
    }
    case UserActions.LOGOUT_ANOTHER: {
      if ('login' in action.payload) {
        const logoutedUser: User = action.payload;
        const activeUsers: User[] = state.active.filter(
          (user) => user.login !== logoutedUser.login
        );
        return {
          active: [...activeUsers],
          inactive: [...state.inactive, logoutedUser],
        };
      }
      return state;
    }
    default:
      return state;
  }
};

export { usersAnother };
