import { ThunkMiddleware } from '@reduxjs/toolkit';
import { ServerActions, UserActions } from '../constants/action-constants';
import { loginAnother, logoutAnother } from '../actions/user-actions';

export const authAnotherUserMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === UserActions.LOGIN_ANOTHER &&
      action.payload.payload &&
      'user' in action.payload.payload
    ) {
      dispatch(loginAnother(action.payload.payload.user));
    }

    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === UserActions.LOGOUT_ANOTHER &&
      action.payload.payload &&
      'user' in action.payload.payload
    ) {
      dispatch(logoutAnother(action.payload.payload.user));
    }

    return next(action);
  };
