import {
  ServerMessage,
  incoming,
  outcoming,
} from '../../store/actions/server-actions';
import { store } from '../../store/store';
import Connection from './connection';

export default class ConnectionHandler {
  private readonly CHECK_PERIOD = 2000;
  private readonly ERROR_MESSAGE_TEXT = 'Unknown server message.';

  private static messageQueu = new ConnectionHandler();

  private connection = new Connection(this.receive.bind(this));
  private waitMessages = new Array<ServerMessage>();
  private idInteval: NodeJS.Timer | null = null;

  private constructor() {
    this.idInteval = this.startCheckWaitMessage();
  }
  static getInstamce() {
    return this.messageQueu;
  }
  send(message: ServerMessage): boolean {
    const result = this.connection.sendMessage(message);
    if (result) {
      store.dispatch(outcoming(message));
      return true;
    }
    this.waitMessages.push(message);
    if (this.idInteval === null) {
      this.idInteval = this.startCheckWaitMessage();
    }
    return false;
  }

  private receive(message: ServerMessage) {
    if (this.isCorrectMessage(message)) {
      this.incomingHandler(message);
    } else {
      console.error(this.ERROR_MESSAGE_TEXT, message.id, message.payload);
    }
  }
  private startCheckWaitMessage() {
    return setInterval(() => {
      this.waitMessages = this.waitMessages.filter(
        (message) => this.send(message) === false
      );
      if (this.waitMessages.length === 0 && this.idInteval !== null) {
        clearInterval(this.idInteval);
        this.idInteval = null;
      }
    }, this.CHECK_PERIOD);
  }
  private incomingHandler(message: ServerMessage) {
    store.dispatch(incoming(message));
  }
  private isCorrectMessage(message: ServerMessage | any): boolean {
    if ('type' in message && 'payload' in message) {
      return true;
    }
    return false;
  }
}
