import React, { ReactNode } from 'react';
import style from './style.module.css';

type FieldGroupProps = {
  header: string;
  children: ReactNode;
};

export const FieldGroup: React.FC<FieldGroupProps> = ({ header, children }) => {
  return (
    <fieldset className={style['fields-group']}>
      <legend>{header}</legend>
      {children}
    </fieldset>
  );
};
