import { ThunkMiddleware } from '@reduxjs/toolkit';
import {
  DialogActions,
  ServerActions,
  UserActions,
} from '../constants/action-constants';
import { sendMessage } from '../../core/services/send-message';
import { User, allActiveUsers } from '../actions/user-actions';

export const setAllActiveUsersMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === UserActions.ALL_ACTIVE &&
      action.payload.payload &&
      'users' in action.payload.payload
    ) {
      dispatch(allActiveUsers(action.payload.payload.users));

      if (Array.isArray(action.payload.payload.users)) {
        action.payload.payload.users.forEach((user: User) => {
          const state = getState();
          if (state.users.userCurrent.login !== user.login) {
            const payload = {
              user: {
                login: user.login,
              },
            };
            sendMessage(DialogActions.MSG_FROM_USER, payload);
          }
        });
      }
    }

    return next(action);
  };
