import { FC, Fragment } from 'react';
import style from './style.module.css';
import Button from '../button/Button';

export type ModalInfoProps = {
  text: string;
  callback: () => void;
};

export const ModalInfo: FC<ModalInfoProps> = ({ text, callback }) => {
  return (
    <Fragment>
      <div className={style.darken}></div>
      <div className={style.modal}>
        <div className={style.wrapper}>
          <label className={style.text}>{text}</label>
          <Button text="Ok" callback={callback} focus={true} />
        </div>
      </div>
    </Fragment>
  );
};
