import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions, ServerActions } from '../constants/action-constants';
import { setMessagesHistory } from '../actions/dialog-actions';

export const setUserMessagesMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === DialogActions.MSG_FROM_USER &&
      action.payload.payload &&
      'messages' in action.payload.payload
    ) {
      if (
        Array.isArray(action.payload.payload.messages) &&
        action.payload.payload.messages.length === 0
      ) {
        return next(action);
      }
      const state = getState();
      if (
        state.users.userCurrent.login ===
        action.payload.payload.messages[0].from
      ) {
        action.payload.payload.login = action.payload.payload.messages[0].to;
      } else {
        action.payload.payload.login = action.payload.payload.messages[0].from;
      }
      dispatch(setMessagesHistory(action.payload.payload));
    }

    return next(action);
  };
