import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions } from '../constants/action-constants';
import { readedMessage } from '../actions/dialog-actions';

export const readedMessageMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (action.payload.type === DialogActions.MSG_READED) {
      dispatch(readedMessage(action.payload.payload.message.id));
    }

    return next(action);
  };
