import { useNavigate } from 'react-router-dom';
import style from './style.module.css';
import React from 'react';
import Button from '../shared/button/Button';

const About: React.FC = () => {
    const backText = 'Вернуться назад';

    const navigate = useNavigate();

    return (
        <main className={style.about}>
            <h3>Веселый чат</h3>
            <label className={style.about__info}>Приложение разработано для демонстрации задания Fun Chat в рамках курса RSSchool JS/FE 2023Q3</label>
            <label className={style.about__info}>Удаление пользователей и сообщений происходит один раз в сутки</label>
            <a className={style.about__link} href='https://github.com/MikAleinik'>Автор MikAleinik</a>
            <Button
                text={backText}
                callback={() => navigate('/main')}
            />
        </main>
    );
};

export default About;