import { useCallback, useState } from 'react';
import { FieldNamedResult } from '../shared/field-named/Field-named';
import { User } from '../../store/actions/user-actions';

export type ValidAuthorization = {
  login: boolean;
  password: boolean;
};
const defaultValue: User = {
  login: '',
  password: '',
  isLogined: false,
};
const defaultValid: ValidAuthorization = {
  login: false,
  password: false,
};

export const useInputChange = (
  value: User = defaultValue,
  valid: ValidAuthorization = defaultValid
): [
  User,
  ValidAuthorization,
  (changedValue: FieldNamedResult) => void,
] => {
  const [userValue, setUserValue] = useState(value);
  const [isValid, setValid] = useState(valid);

  const changeHandler = useCallback((changedValue: FieldNamedResult) => {
    setUserValue((value) => ({
      ...value,
      [changedValue.name]: changedValue.value,
    }));
    setValid((isValid) => ({
      ...isValid,
      [changedValue.name]: changedValue.isValid,
    }));
  }, []);

  return [userValue, isValid, changeHandler];
};
