import { ServerMessage } from '../../store/actions/server-actions';
import {
  connectionClose,
  connectionError,
  connectionOpen,
} from '../../store/actions/socket-actions';
import { store } from '../../store/store';

export default class Connection {
  private connection: WebSocket | null = null;

  constructor(callback: (message: ServerMessage) => void) {
    if (this.connection === null) {
      this.getNewConnection(callback);
    }
  }

  sendMessage(message: ServerMessage): boolean {
    if (
      this.connection !== null &&
      this.connection.readyState === WebSocket.OPEN
    ) {
      // console.log('OUT', message);

      this.connection.send(JSON.stringify(message));
      return true;
    }
    return false;
  }

  private getNewConnection(callback: (message: ServerMessage) => void) {
    const connection = new WebSocket(`${process.env.REACT_APP_SERVER_URL}`);
    connection.addEventListener('open', () => {
      this.connection = connection;
      this.setListeners(connection, callback);
      store.dispatch(connectionOpen());
    });
    connection.addEventListener('error', () => {
      store.dispatch(connectionError());
      this.getNewConnection(callback);
    });
  }

  private setListeners(
    connection: WebSocket,
    callback: (message: ServerMessage) => void
  ) {
    connection.addEventListener('message', (message: MessageEvent) => {
      const deserializedMessage: ServerMessage = JSON.parse(message.data);
      // console.log('IN', deserializedMessage);

      callback(deserializedMessage);
    });
    connection.addEventListener('close', () => {
      store.dispatch(connectionClose());
      this.getNewConnection(callback);
    });
  }
}
