export const getTranslated = (value: string) => {
  const findResult = serverAnswer.find(
    (item) => item[0].toLowerCase() === value.toLowerCase()
  );
  if (findResult) {
    return findResult[1];
  }

  return value;
};

const serverAnswer = [
  ['Unknown server error', 'Не известная ошибка сервера'],
  [
    'A user with this login is already authorized',
    'Пользователь с таким логином уже авторизован',
  ],
  [
    'Password length must be more than 4 characters',
    'Пароль должен быть более 4 символов',
  ],
  [
    'Login length must be more than 4 characters',
    'Логин должен быть более 4 символов',
  ],
  [
    'A user with this login already exists',
    'Пользователь с таким логином уже существует',
  ],
  [
    'There is no user with this login',
    'Пользователь с таким логином не существует',
  ],
  ['Incorrect password', 'Не верный пароль'],
];
