import { User, UserAction } from '../../actions/user-actions';
import { UserActions } from '../../constants/action-constants';

const initialState: User = {
  login: null,
  password: null,
  isLogined: false,
};

const userCurrent = (state = initialState, action: UserAction): User => {
  switch (action.type) {
    case UserActions.REGISTRATION:
    case UserActions.LOGIN: {
      if (!Array.isArray(action.payload) && action.payload.login) {
        return {
          ...action.payload,
        };
      }
      return state;
    }
    case UserActions.LOGOUT: {
      return initialState;
    }
    default:
      return state;
  }
};

export { userCurrent };
