import { DialogActions } from '../constants/action-constants';

export type Dialog = {
  login: string | null;
  messages: Message[];
};

export type Message = {
  id: string | null;
  from: string | null;
  to: string | null;
  text: string;
  datetime: number;
  status: MessageStatus;
};

export type MessageStatus = {
  isDelivered: boolean;
  isReaded: boolean;
  isEdited: boolean;
};

export type DialogAction = {
  type: DialogActions;
  payload: Dialog;
};

export type MessageAction = {
  type: DialogActions;
  payload: string;
};

export const selectMessage = (idMessage: string): MessageAction => ({
  type: DialogActions.SELECT_MESSAGE,
  payload: idMessage,
});

export const unSelectMessage = (): MessageAction => ({
  type: DialogActions.UNSELECT_MESSAGE,
  payload: '',
});

export const clearMessagesHistory = (): DialogAction => ({
  type: DialogActions.MSG_CLEAR_HISTORY,
  payload: {
    login: '',
    messages: [],
  },
});

export const setMessagesHistory = (dialog: Dialog): DialogAction => ({
  type: DialogActions.MSG_FROM_USER,
  payload: {
    login: dialog.login,
    messages: dialog.messages,
  },
});

export const addMessageToHistory = (
  login: string,
  message: Message
): DialogAction => ({
  type: DialogActions.MSG_SEND,
  payload: {
    login: login,
    messages: [message],
  },
});

// TODO изменить на проброс только id
export const deliveredMessage = (idMessage: string): DialogAction => ({
  type: DialogActions.MSG_DELIVERED,
  payload: {
    login: null,
    messages: [
      {
        id: idMessage,
        from: null,
        to: null,
        text: '',
        datetime: 0,
        status: {
          isDelivered: true,
          isReaded: false,
          isEdited: false,
        },
      },
    ],
  },
});

