import { useEffect, useState } from 'react';
import ConnectionHandler from '../../core/connection/connection-handler';
import IdGenerator from '../../core/id-generator/id-generator';
import { ServerMessage } from '../../store/actions/server-actions';
import { UserActions } from '../../store/constants/action-constants';
import { RootState } from '../../store/store';
import { useSelector } from 'react-redux';
// import { AuthResult, isAuthResult } from '../auth/auth-types';
import { getTranslated } from '../../core/language/language';
import { User } from '../../store/actions/user-actions';

//TODO вынести при переиспользовании
export const enum DefferedStatus {
  NONE = 'none',
  PENDING = 'pending',
  FULLFILLED = 'fullfilled',
  REJECTED = 'rejected',
}

export type AuthCallback = {
  pending: () => void;
  fullfilled: () => void;
  rejected: () => void;
};

export const useAuthorization = (
  userValue: User,
  typeAction: UserActions,
  callback?: AuthCallback
): [string, () => void, () => void] => {
  const [status, setStatus] = useState(DefferedStatus.NONE);

  const [messageId, setMessageId] = useState('');
  const messages = useSelector((store: RootState) => store.server.incoming);

  const [messageResult, setMessageResult] = useState('');

  useEffect(() => {
    if (status !== DefferedStatus.PENDING) {
      return;
    }
    const lastMessage = messages.find((message) => message.id === messageId);
    if (lastMessage) {
      if (lastMessage.type === typeAction) {
        setStatus(DefferedStatus.FULLFILLED);
      }
      if (lastMessage.payload && lastMessage.payload.error) {
        setStatus(DefferedStatus.REJECTED);
        const translatedMessage = getTranslated(lastMessage.payload.error);
        setMessageResult(translatedMessage);
      }
    }
  }, [messageId, messages, status]);

  useEffect(() => {
    switch (status) {
      case DefferedStatus.PENDING: {
        callback?.pending();
        break;
      }
      case DefferedStatus.FULLFILLED: {
        callback?.fullfilled();
        break;
      }
      case DefferedStatus.REJECTED: {
        callback?.rejected();
        break;
      }
    }
  }, [callback, status]);

  const authSendHandler = () => {
    const id = IdGenerator.getId();
    setMessageId(id);
    const message: ServerMessage = {
      id: id,
      type: typeAction,
      payload: {
        user: {
          login: userValue.login,
          password: userValue.password,
          isLogined: typeAction === UserActions.LOGIN ? true : false,
        },
      },
    };
    const connectionHandler = ConnectionHandler.getInstamce();
    connectionHandler.send(message);

    setStatus(DefferedStatus.PENDING);
  };

  const reset = () => {
    setStatus(DefferedStatus.NONE);
  };

  return [messageResult, authSendHandler, reset];
};
