import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions, UserActions } from '../constants/action-constants';
import { sendMessage } from '../../core/services/send-message';

export const getMessagesMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (action.type === UserActions.SELECT_USER) {
      const payload = {
        user: {
          login: action.payload.login,
        },
      };
      sendMessage(DialogActions.MSG_FROM_USER, payload);
    }

    return next(action);
  };
