import { useEffect, useState } from 'react';

export type ValidationResult = {
  result: boolean;
  message: string;
};

export const useValidation = (
  value: string,
  valueEqual: string = '',
  validators: Array<
    (value: string, valueEqual: string) => ValidationResult
  > = []
): [string[], boolean] => {
  const [isValid, setValid] = useState(false);
  const [errors, setErrors] = useState(new Array<string>());

  useEffect(() => {
    let result = true;
    let err = new Array<string>();
    validators.forEach((validator) => {
      const isChecked = validator(value, valueEqual);
      if (!isChecked.result) {
        result = false;
      }
      if (isChecked.message !== '') {
        err.push(isChecked.message);
      }
    });

    setErrors(err);
    setValid(result);
  }, [validators, value, valueEqual]);

  return [errors, isValid];
};
