import style from './style.module.css';
import React from 'react';

type ButtonProps = {
  text: string;
  callback: () => void;
  focus?: boolean;
  disabled?: boolean;
  small?: boolean;
  fixed?: boolean;
  submit?: boolean;
};

const Button: React.FC<ButtonProps> = ({
  text,
  callback,
  focus = false,
  disabled = false,
  small = false,
  fixed = false,
  submit = false,
}) => {
  const styles = [style.button];
  if (small) {
    styles.push(style.small);
  }
  if (fixed) {
    styles.push(style.fixed);
  }
  return (
    <button
      type={submit ? "submit" : "button"}
      className={`${styles.join(' ')}`}
      onClick={callback}
      disabled={disabled}
      autoFocus={focus}
    >
      {text}
    </button>
  );
};

export default Button;
