import ConnectionHandler from "../connection/connection-handler";
import IdGenerator from "../id-generator/id-generator";
import { ServerMessage } from "../../store/actions/server-actions";
import { DialogActions } from "../../store/constants/action-constants";

export const sendMessage = (action: DialogActions, payload: any) => {
  const id = IdGenerator.getId();
  const message: ServerMessage = {
    id: id,
    type: action,
    payload: payload,
  };
  const connectionHandler = ConnectionHandler.getInstamce();
  connectionHandler.send(message);
};
