import { MessageAction } from '../../actions/dialog-actions';
import { DialogActions } from '../../constants/action-constants';

const initialState: string | null = null;

const messageSelected = (
  state = initialState,
  action: MessageAction
): string | null => {
  switch (action.type) {
    case DialogActions.SELECT_MESSAGE: {
      return action.payload;
    }
    case DialogActions.UNSELECT_MESSAGE: {
      return null;
    }
    default:
      return state;
  }
};

export { messageSelected };
