import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions } from '../constants/action-constants';
import { deliveredMessage } from '../actions/dialog-actions';

export const deliveredMessageMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (action.payload.type === DialogActions.MSG_DELIVERED) {
      dispatch(deliveredMessage(action.payload.payload.message.id));
    }

    return next(action);
  };
