import { Dialog, DialogAction } from '../../actions/dialog-actions';
import { DialogActions } from '../../constants/action-constants';

const initialState = new Array<Dialog>();

const messages = (state = initialState, action: DialogAction) => {
  switch (action.type) {
    case DialogActions.MSG_CLEAR_HISTORY: {
      return initialState;
    }
    case DialogActions.MSG_FROM_USER: {
      const dialogs = state.filter(
        (messages) => messages.login !== action.payload.login
      );
      return [...dialogs, action.payload];
    }
    case DialogActions.MSG_SEND: {
      const dialogsClone: Dialog[] = JSON.parse(JSON.stringify(state));
      let isPushed = false;
      dialogsClone.forEach((dialog) => {
        if (dialog.login === action.payload.login) {
          dialog.messages.push(action.payload.messages[0]);
          isPushed = true;
        }
      });
      if (!isPushed) {
        const newDialog: Dialog = {
          login: action.payload.login,
          messages: [action.payload.messages[0]],
        };
        dialogsClone.push(newDialog);
      }
      return [...dialogsClone];
    }
    case DialogActions.MSG_DELIVERED: {
      const dialogsClone: Dialog[] = JSON.parse(JSON.stringify(state));
      dialogsClone.forEach((dialog) => {
        dialog.messages.forEach((message) => {
          if (message.id === action.payload.messages[0].id) {
            message.status.isDelivered = true;
          }
        });
      });
      return [...dialogsClone];
    }
    case DialogActions.MSG_READED: {
      const dialogsClone: Dialog[] = JSON.parse(JSON.stringify(state));
      dialogsClone.forEach((dialog) => {
        dialog.messages.forEach((message) => {
          if (message.id === action.payload.messages[0].id) {
            message.status.isReaded = true;
          }
        });
      });
      return [...dialogsClone];
    }
    case DialogActions.MSG_DELETE: {
      const dialogsClone: Dialog[] = JSON.parse(JSON.stringify(state));
      dialogsClone.forEach((dialog) => {
        let indexMessage = -1;
        dialog.messages.forEach((message, index) => {
          if (message.id === action.payload.messages[0].id) {
            indexMessage = index;
          }
        });
        if (indexMessage !== -1) {
          dialog.messages.splice(indexMessage, 1);
        }
      });
      return [...dialogsClone];
    }
    case DialogActions.MSG_EDIT: {
      const dialogsClone: Dialog[] = JSON.parse(JSON.stringify(state));
      dialogsClone.forEach((dialog) => {
        dialog.messages.forEach((message) => {
          if (message.id === action.payload.messages[0].id) {
            message.text = action.payload.messages[0].text;
            message.status.isEdited =
              action.payload.messages[0].status.isEdited;
          }
        });
      });
      return [...dialogsClone];
    }
    default: {
      return state;
    }
  }
};

export { messages };
